//Тут храняться миксины

@mixin test() {
    background-color: red;
}

@mixin section-header() {
    font-family: 'PF Agora Sans Pro';
    font-style: normal;
    font-weight: 1000;
    font-size: 40px;
    line-height: 110%;
    color: $main-dark;

    @media screen and (max-width: 880px) {
        font-weight: 1000;
        font-size: 28px;
        line-height: 110%;
    }
}
@mixin section-text() {
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    
    color: #313131;
}
@mixin section-button($color) {
    cursor: pointer;
    display: inline-block;
    padding: 16px 28px;
    box-sizing: border-box;
    border: none;
    outline: none;
    background-color: $color;
    border-radius: 120px;

    font-family: 'PF Agora Sans Pro';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 100%;
    text-align: center;
    color: #FFFFFF;  
    border: 2px solid $color;

    transition: .3s;
    
    &:hover, &._active {
        color: $color;
        background: transparent;
    }

}