@import "mixins";
@import "fonts";
@import "vendor";
@import "vars";
@import "settings";
// @import "~bootstrap/scss/bootstrap";

    * {
        font-family: 'Arial';
        font-weight: 400;
        font-style: normal;
        // color: $main-dark;
        text-decoration: none;

        min-height: 0;
        min-width: 0;
    }
    .container {
        max-width: 960px;
        width: 100%;
        box-sizing: border-box;
        margin: 0 auto;
    }   
    .js-marquee, .js-marquee-wrapper {
        display: flex;
    }
    .section__header {
        @include section-header();
    }
    .section-text {
        @include section-text();
    }
    .btn {
        & {
            @include section-button($main-color);
        }
        &._dark {
            @include section-button($main-button-dark);
        }
        &._red {
            @include section-button($main-button-red);
        }
    }

    .header {
        padding: 20px 0 0;
        margin-bottom: 40px;
        .container {

        }
        .header__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;

            position: relative;
        }
        .header__burger-wrap {

        }
        .header__burger {
            position: relative;
            display: flex;
            justify-content: center;
            flex-direction: column;
            gap: 6px;
            width: 40px;
            height: 30px;
            cursor: pointer;

            &::before, &::after {
                transition: .3s;
                content: '';
                display: block;
                width: inherit;
                height: 6px;
                border-radius: 10px;
                background: $main-color;
                position: absolute;
                top: 0;
                left: 0;
            }
            &::after {
                top: unset;
                bottom: 0;
            }

            &._active {
                .header__burger__item {
                    opacity: 0;
                }
                &::before, &::after {
                    top: 50%;
                    transform: translateY(-50%) rotate(45deg);
                }
                &::after {
                    transform: translateY(-50%) rotate(-45deg);
                }
            }
        }
        .header__burger__item {
            transition: .3s;
            width: 100%;
            height: 6px;
            border-radius: 10px;
            background: $main-color;
        }
        .header__burger__list-wrap {
            display: none;
            position: absolute;
            top: 101%;
            left: 0;
            max-width: 300px;
            width: 100%;
            box-sizing: border-box;

            background: #FFFFFF;
            /* Главная/Кнопки и элементы/ синий */
            border-width: 0px 10px 10px 0px;
            border-style: solid;
            border-color: #0B2E59;
            box-shadow: 15px 15px 20px rgba(0, 0, 0, 0.15);
            border-radius: 0px 0px 10px 0px;   
            
            padding: 30px 10px 24px 0;
            z-index: 100;
        }
        .header__burger__list {
            margin-bottom: 42px;
        }
        .header__burger__list__headline {
            display: flex;
            justify-content: start;
            align-items: center;

            font-family: 'PF Agora Sans Pro';
            font-weight: 1000;
            font-size: 20px;
            line-height: 120%;
            /* identical to box height, or 24px */
            text-transform: uppercase;
            /* Главная/текст/синий */
            color: $main-dark;    
            padding-bottom: 11px;
            border-bottom: 1px solid $main-dark;
            margin-bottom: 10px;

            img {
                margin-right: 7px;
                flex-shrink: 0;
            }
        }
        .header__burger__list__item {
            padding-left: 45px;
            a {
                font-family: 'PF Agora Sans Pro';
                font-style: normal;
                font-weight: 500;
                font-size: 18px;
                line-height: 120%;
                color: $main-dark;                
            }
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
        .header__burger__lk-wrap {
            display: flex;
            justify-content: start;
            align-items: center;
            gap: 20px;
            img {

            }
            .header__burger__lk-container {

            }
            .header__burger__lk__name {
                font-family: 'PF Agora Sans Pro';
                font-style: normal;
                font-weight: 1000;
                font-size: 20px;
                line-height: 120%;
                /* identical to box height, or 24px */
                text-transform: uppercase;
                /* Главная/текст/голубой */
                color: $main-color                
            }
            .header__burger__lk__goal {
                font-family: 'PF Agora Sans Pro';
                font-style: normal;
                font-weight: 1000;
                font-size: 16px;
                line-height: 100%;
                /* identical to box height, or 16px */
                /* Главная/текст/голубой */
                color: #00B5E7; 
                
                display: flex;
                justify-content: start;
                align-items: center;
                gap: 5px;
                
                &::before {
                    content: '';
                    width: 17px;
                    height: 17px;
                    display: block;
                    box-sizing: border-box;
                    background-image: url('../img/goal.svg');
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }
        }
        .header__wrap {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 40px;
        }
        .header__link {
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 1000;
            font-size: 20px;
            line-height: 120%;
            /* identical to box height, or 24px */
            text-transform: uppercase;
            /* Главная/текст/голубой */
            color: $main-color;            
        }
        .logo {
            img {

            }
            ._mob {
                display: none;
            }
        }
        .header__auth-wrap {

        }
        .header__auth-link {

            span {
                font-family: 'PF Agora Sans Pro';
                font-style: normal;
                font-weight: 500;                
                color: $main-color;
                position: relative;
                bottom: 4px;
            }
            &._unauthorised, &._authorised {
                &:hover .header__auth-link__icon {
                    box-shadow: 0 0 5px 0px $main-color, 0 0 3px -1px $main-color inset;
                    overflow: hidden;
                }
            }
            &._authorised {
                .header__auth-link__icon {
                    border: 2px solid $main-color;
                    background: transparent;
                    color: $main-color;

                    svg {
                        bottom: -3px;
                    }
                }
                span {
                    display: none;
                }
            }
        }
        .header__auth-link__icon {
            width: 40px;
            height: 40px;
            background: $main-color;
            border-radius: 50%;
            color: #fff;
            position: relative;
            transition: .3s;
            overflow: hidden;
            box-sizing: border-box;
            svg {
                width: 25px;
                height: 36px;
                position: relative;
                position: absolute;
                bottom: 0;
                left: 50%;
                transform: translateX(-50%);
            }
        }
        
        .header__schedule {
            background: $main-dark;
            padding: 5px 20px;

            max-width: 960px;
            width: 100%;
            margin: 5px auto 0;
            box-sizing: border-box;
            overflow: hidden;
        }
        .header__schedule__wrap {
            display: flex;
            justify-content: start;
            align-items: center;
            gap: 100px;
        }
        .header__schedule__item {
            display: flex;
            justify-content: start;
            align-items: center;
            width: 281px;
            flex-shrink: 0;
        }
        .header__schedule__item__eyes {
            display: flex;
            justify-content: start;
            align-items: center;
            gap: 9px;
            margin-right: 10px;
        }
        .eye {
            height: 20px;
            width: 20px;
            background: #fff;
            border-radius: 50%;
            position: relative;

            &::after {
                content: '';
                width: 10px;
                height: 10px;
                border-radius: 50%;
                background: #313131;
                display: block;

                position: absolute;
                bottom: 4px;
                right: 2px;
            }
        }
        .header__schedule__item__text {
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 900;
            font-size: 14px;
            line-height: 100%;
            /* or 14px */
            /* Главная/текст/голубой */
            color: $main-color;
            margin-right: 5px;
        }
        .header__schedule__item__name {
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 14px;
            /* or 14px */
            /* Главная/текст/голубой */
            color: #fff;
            padding-top: 2px;
        }
    }

    .title {
        overflow-x: hidden;
        .container {

        }
        .title__wrapper {
            position: relative;
        }
        .title__slider {

        }
        .title__slide {
            height: 300px;
            img {
                width: inherit;
                height: 100%;
                object-fit: cover;
            }
            ._mob {
                display: none;
            }
        }
        .title__wrapper__dots {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 20px;
            background: rgba(255, 255, 255, 0.4);
            border-radius: 102px;
            box-sizing: border-box;
            padding: 0 7px;

            .slick-dots {
                position: static;
                li {
                    margin: 0 2px;
                }
            }
            .slick-dots li button::before {
                font-size: 15px;
                color: $main-color;
                opacity: 1;
            }
            .slick-dots li.slick-active button::before {
                color: $main-dark;
                opacity: 1;
            }
        }
    }

    .welcome {
        margin-bottom: 80px;
        .container {

        }
        .welcome__wrapper {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 40px;
            padding: 80px 0;

            &._secondary {
                background: rgba(0, 181, 231, .3);
                padding: 27px 20px;
                box-sizing: border-box;
                position: relative;
            }
        }
        .welcome__hero {
            position: absolute;
            bottom: 0;
            right: 0;
        }
        .welcome__left {

        }
        .ghosts__list {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            gap: 14px;
        }
        .ghosts__item {
            height: 242px;
            position: relative;

            &:nth-child(1) .ghost{
                transform: translate3d(-50%, -10%, 0);
                animation: spin 9s infinite linear;
            }
            &:nth-child(2) .ghost{
                transform: translate3d(-50%, -10%, 0);
                animation: spin 5s infinite linear;
            }
            &:nth-child(3) .ghost{
                transform: translate3d(-50%, 10%, 0);
                animation: spin 8s infinite linear;
            }
        }
        .ghost {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate3d(-50%, -10%, 0);
        }
        .shadow {
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }

        .welcome__right {

        }
        .welcome__headline {
            margin-bottom: 10px;
        }
        .welcome__desc {
            font-family: 'Arial';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;  
        }
        .welcome__btn {
            margin-top: 10px;
            background: $main-dark;
            border-color: $main-dark;

            &:hover {
               background: transparent;
               color: $main-dark;
            }
        }
        .welcome__right__socials {
            margin-top: 20px;
            display: flex;
            justify-content: start;
            align-items: center;
        }
        .welcome__right__socials__text {
            max-width: 163px;

            font-weight: 700;
            font-size: 20px;
            line-height: 120%;
            
            color: $main-color;            
        }
        .welcome__right__social__link {
            width: 50px;
            height: 50px;
            flex-shrink: 0;
            color: $main-color;

            display: flex;
            justify-content: center;
            align-items: center;

            border: 2px solid $main-color;
            border-radius: 50%;

            svg {
                width: 29px;
                height: 26px;
            }
            &:not(:last-child) {
                margin-right: 10px;
            }
        }
    }

    .video {
        margin-bottom: 80px; 
        .container {

        }
        .video__wrapper {

        }
        .video-box {
            height: 538px;

            video {
                display: block;
                width: 100%;
                object-fit: contain;
            }
            // img {
            //     display: block;
            //     width: 100%;
            // }
         }
        .video__links {
            margin-top: 20px;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 40px;
        }
        .video__link {
            max-width: 280px;
            width: 100%;

        }
    }

    .categories {
        margin-bottom: 80px;
        .container {

        }
        .categories__list {
            display: grid;
            grid-template-columns: repeat(auto-fit, 300px);
            flex-wrap: wrap;
            gap: 60px 30px;
            justify-content: center;
        }
        .categories__item {
            &:nth-child(1) .categories__overlay {

            }
            &:nth-child(2) .categories__overlay {
                background: rgba(223, 31, 115, 0.95);
            }
            &:nth-child(3) .categories__overlay {
                background: rgba(255, 210, 1, 0.97);
            }
            &:nth-child(3) span {
                color: $main-dark;
            }
        }
        .categories__link {
            display: block;
            width: 100%;
            height: 100%;
            position: relative;
            img {

            }
            ._mob {
                display: none;
            }
            &:hover .categories__overlay {
                opacity: 1;
            }
        }
        .categories__overlay {
            transition: .3s;
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 181, 231, 0.95);
            padding: 55px 57px 25px 27px;
            box-sizing: border-box;   
            
            display: flex;
            justify-content: start;
            align-items: stretch;
            flex-direction: column;
        }
        .categories__header {
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 1000;
            font-size: 30px;
            line-height: 110%;
            text-transform: uppercase;
            color: #FFFFFF;
            display: block;
        }
        .categories__desc {
            display: block;
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 1000;
            font-size: 14px;
            line-height: 130%;
            /* or 18px */
            text-align: left;
            color: #FFFFFF;    
            margin-top: 10px; 
            text-transform: uppercase;  

        }
        .categories__text {
            display: block;
            margin-top: 17px;

            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            /* or 21px */
            color: #FFFFFF;            
        }
        .more {
            font-weight: 400;
            font-size: 14px;
            line-height: 110%;
            /* or 15px */
            color: rgba(255, 255, 255, 0.7);    
            display: flex;
            justify-content: start;
            align-items: center;
            margin-top: auto; 
            
            svg {
                width: 48px;
                height: 7px;
                margin-left: 5px;
                color: inherit;
            }
        }
    }
    .parents {
        margin-bottom: 151px;
        .container {

        }
        .parents__wrapper {

        }
        .parents__controlls {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 40px;
        }
        .parents__headline {
            max-width: 306px;
            text-transform: uppercase;
        }
        .parents__tabs__list {
            flex-shrink: 0;

            display: flex;
            justify-content: end;
            align-items: center;
            gap: 10px;
        }
        .parents__tabs__item {
            flex-shrink: 0;
            max-width: 145px;
            width: 100%;

            display: flex;
            justify-content: center;
            align-items: center;
            gap: 5px;

            &:first-child {
                background-color: $main-dark;
                border-color: $main-dark;

                &:hover, &._active {
                    color: $main-dark;
                    background:transparent;
                }
            }
            &:not(:first-child) {
                padding: 11px 16px;
            }

            &:hover {
                svg {
                    color: inherit;
                }
            }
            svg {
                flex-shrink: 0;
                width: 27px;
                height: 28px;
            }
        }
        .parents__slider__wrapper {
            position: relative;
        }
        .parents__slider__controlls {
            
        }
        .parents__slider__controll {
            position: absolute;
            top: 50%;
            right: 17px;
            transform: translateY(-50%);
            z-index: 10;

            border: none;
            outline: none;
            width: 60px;
            height: 60px;
            box-sizing: border-box;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background: transparent;
            padding: 0 0 0 5px;

            transition: .3s;
            cursor: pointer;

            &:hover {
                background: $main-color;
            }
        }
        .parents__slider {
            padding-right: 100px;
            box-sizing: border-box;

            .slick-track {
                margin-left: 0;
            }
        }
        .parents__slide {
            margin-right: 40px;

            // &._active[data-slick-index="1"] {
            //     transform: translate3d(0, 0, 0);
            //     animation: slideIn 1.1s 1 ease-in;
            // }
            // &._active[data-slick-index="2"] {
            //     transform: translate3d(0, 0, 0);
            //     animation: slideIn 1s 1 ease-in;
            // }

            &._active.slick-slide.slick-active {
                transform: translate3d(0, 0, 0);
                animation: slideIn 1.1s 1 ease-in;
            }
            // &._active[data-slick-index="2"] {
            //     transform: translate3d(0, 0, 0);
            //     animation: slideIn 1s 1 ease-in;
            // }
        }
        .parents__slide__img-box {
            height: 140px;
            img {
                width: 100%;
                height: 140px;
                object-fit: cover;
            }
        }
        .parents__slide__wrapper {
            background: #DDF3FF;
            border-radius: 3px;       
            box-sizing: border-box;
            padding: 23px 18px 20px 20px;

            display: flex;
            justify-content: start;
            align-items: flex-start;
            flex-direction: column;

            span {
                display: block;
            }
        }
        .parents__slide__name {
            font-family: 'PF Agora Sans Pro';
            font-style: normal;
            font-weight: 600;
            font-size: 19px;
            line-height: 120%;
            color: $main-dark;
            margin-bottom: 10px;
            min-height: 48px;

            span {
                display: block;
                font-family: 'PF Agora Sans Pro';
                font-style: normal;
                font-weight: 500;
                font-size: 14px;
                line-height: 16px;
                /* identical to box height, or 114% */
                color: #A0A0A0;      
                margin-top: 2px;          
            }
        }
        .parents__slide__desc {
            font-weight: 400;
            font-size: 16px;
            line-height: 130%;
            
            /* or 21px */
            
            /* Главная/текст/черный */
            color: #313131;
            min-height: 102px;
        }
        .parents__slide__link {
            margin-top: 10px;
        }
    }
    .blog {
        .container {

        }
        .blog__wrapper {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 40px;
        }
        .blog__left-img {
            height: 307px;
            img {
                width: 100%;
                height: 100%;
            }
        }
        .blog__right {
            position: relative;

            
        }
        .blog__crab {
            position: absolute;
            bottom: 93%;
            right: 0;
            z-index: -1;

            .blog__crab__eye {
                position: absolute;
                top: 28px;
                left: 68px;
                transition: .3s;

                width: 17px;
                height: 17px;
                box-sizing: border-box;
                background: #C21023;
                border-radius: 50%;
                display: block;

                animation: blink 3s infinite linear;
                opacity: 0;

                &:nth-child(2) {
                    top: 26px;
                    left: 114px;   
                    animation: blink 3s infinite linear;
                }

                &._paused {
                    animation-play-state: paused;
                }
            }
            .blog__crab__arm {
                position: absolute;

                transition: .3s;

                width: 47px;
                height: 47px;
                box-sizing: border-box;
                background: #C21023;
                border-radius: 50%;
                display: block;

                &._left {
                    opacity: 0;
                    top: 20px;
                    left: 9px;
                }
                &._right {
                    opacity: 0;
                    top: 10px;
                    left: 138px;
                    animation-delay: .5s;
                }

                animation: clanck 2s infinite linear;

                &._paused {
                    animation-play-state: paused;
                }
            }
        }
        .blog__headline {
            display: block;
            text-transform: uppercase;
            margin-bottom: 20px;
        }
        .blog__desc {
            font-weight: 400;
            font-size: 20px;
            line-height: 130%;
            
            color: #313131;            
            span {
                font-weight: 700;
            }
        }
        .blog__text-link {
            display: block;
            margin-top: 10px;

            font-weight: 700;
            font-size: 16px;
            line-height: 130%;
            /* identical to box height, or 21px */
            text-decoration-line: underline;
            /* Главная/Кнопки и элементы/голубой */
            color: $main-color;            
        }
        .blog__main-link {
            margin-top: 40px;
        }
    }

    .footer{
        margin-top: 193px;
        background: $main-color;
        .container {
            padding: 20px 0 40px;
            position: relative;
            z-index: 10;
        }
        .footer-img__wrap {
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: -10;
            background: $main-color;
         }
        .footer-img {
            position: absolute;
            bottom: 48px;
            right: 0;
            z-index: -20;
        }
        .footer__wrapper {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
        }

        .footer__left {
            flex-shrink: 0;
        }
        .footer__left__wrap {
            display: flex;
            justify-content: start;
            align-items: center;
            margin-bottom: 19px;
        }
        .logo {
            flex-shrink: 0;
            max-width: 130px;
            display: block;
            margin-right: 30px;
            img {
                display: block;
                width: 100%;
                object-fit: contain;
            }
        }
        .social {
            flex-shrink: 0;
            width: 50px;
            height: 50px;
            border: 2px solid #fff;
            border-radius: 50%;

            display: flex;
            justify-content: center;
            align-items: center;
            color: #fff;

            &:not(:last-child) {
                margin-right: 20px;
            }

            img {
                display: block;
                width: 29px;
                object-fit: contain;
            }

            svg {
                width: 29px;
                height: 26px;
            }
        }
        .mail {
            font-family: 'PF Agora Sans Pro';
