    @media screen and (max-width: 550px) {
        .blog .blog__left-img img {
            object-fit: contain;
        }
        .blog .blog__left-img {
            top: 80px;
        }
    }
    @media screen and (max-width: 536px) {
        .parents .parents__slider {
            padding-right: 80px;
        }
        .footer .logo {
            order: 5;
            margin-right: 0;
            margin-top: 15px;
        }
        .welcome .welcome__right__socials {
