    @media screen and (max-width: 375px) {
        .blog .blog__headline {
            margin-bottom: 265px;
        }
        .parents__slide {
            opacity: .3;

            &.slick-active.slick-current {
                opacity: 1;
            }
        } 
        .parents .parents__slider__controll {
            right: 35px;
            width: 40px;
            height: 40px;
            padding: 10px;
            box-sizing: border-box;

            img {
                display: block;
                width: 100%;
            }
        }
        .parents .parents__slide {
            width: 260px;
            margin-right: 80px;
        }
        .parents .parents__slider {
            padding-right: 0;
        }
        .parents .container {
            padding-right: 0;

        }
    }

    @keyframes slideIn {
        0% {
          transform: translate3d(-100%, 0, 0);
          opacity: 0;
        }
        45% {
          transform: translate3d(0, 0, 0);
          opacity: 1;
        }
        50% {
          transform: translate3d(10%, 0, 0);
        }
        100% {
          transform: translate3d(0, 0, 0);
        }
    }
    @keyframes spin {
        0% {
          transform: translate3d(-50%, -10%, 0);
        }
        50% {
          transform: translate3d(-50%, 5%, 0);
        }
        100% {
          transform: translate3d(-50%, -10%, 0);
        }
    }
    @keyframes blink {
        20%, 100% {
          opacity: 0;
        }
        0% {
          opacity: 0;
        }
        10% {
          opacity: 1;
        }
      }
      @keyframes clanck {
        20%, 100% {
